<style lang='stylus'>
.logo{
  height: 79px;
  background: #F33E46;
  line-height 79px;
  color: #ffffff;
  font-size: 18px;

  img{
    width: 54px;
    height: 54px;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.4);
    display: inline-block;
    vertical-align: middle;
    margin-left: 14px;
  }
}
.el-menu-item-group ul li{
  background: #283246 !important;
  &:hover{
    background: #32435F !important;
  }
  // margin-left: 20px;
}
</style>
<template>
<el-row class="tac">
  <el-col :span="24">
    <el-menu
      default-active="1"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      router
      unique-opened
      background-color="#263753"
      text-color="#ffffff"
      active-text-color="rgb(62,144,237)"
    >
    <div class='logo'>
      <img src="" alt="">
      福星社管理平台</div>
      <el-submenu index="1">
        <template slot="title">
          数据概览</template>
        <el-menu-item-group>
          <el-menu-item index="1-1">选项1</el-menu-item>
          <el-menu-item index="1-2">选项2</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">福利企业</template>
        <el-menu-item-group>
          <el-menu-item index="2-1">选项1</el-menu-item>
          <el-menu-item index="2-2">选项2</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="3">
        <template slot="title">
          <img src="../../static/icons/aside/manage_xuan.png" alt="" ref='img_3'>
          福利管理</template>
      <el-menu-item-group>
        <el-menu-item index="/welfaremanagement" ref='manage_1'>
        福利管理</el-menu-item>
        <el-menu-item index="3-2">选项2</el-menu-item>
      </el-menu-item-group>
      </el-submenu>
      <el-submenu index="4">
        <template slot="title">赚取佣金</template>
        <el-menu-item-group>
          <el-menu-item index="4-1">选项1</el-menu-item>
          <el-menu-item index="4-2">选项2</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="5">
        <template slot="title">订单管理</template>
        <el-menu-item-group>
          <el-menu-item index="5-1">选项1</el-menu-item>
          <el-menu-item index="5-2">选项2</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="6">
        <template slot="title">优惠券</template>
        <el-menu-item-group>
          <el-menu-item index="6-1">选项1</el-menu-item>
          <el-menu-item index="6-2">选项2</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="7">
        <template slot="title">财务管理</template>
        <el-menu-item-group>
          <el-menu-item index="7-1">选项1</el-menu-item>
          <el-menu-item index="7-2">选项2</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="8">
        <template slot="title">福星中心</template>
        <el-menu-item-group>
          <el-menu-item index="8-1">选项1</el-menu-item>
          <el-menu-item index="8-2">选项2</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-submenu index="9">
        <template slot="title">
          <img src="../../static/icons/aside/manage_wei.png" alt="">
          系统设置</template>
        <el-menu-item-group>
          <el-menu-item index="9-1">选项1</el-menu-item>
          <el-menu-item index="9-2">选项2</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
    </el-menu>
  </el-col>
  {{a}}
</el-row>

</template>
<script>
import manage_image from '../assets/icons/aside/manage_wei.png'
import manage_change_image from '../assets/icons/aside/manage_xuan.png'
export default {
  data () {
    return {
      a: 1,
      border_1: 'border-right: 6px solid #F23E46'
    }
  },
  methods: {
    handleOpen (key, keyPath) {
      this.a = parseInt(key)
      // console.log(this.a)
      if (this.a === 3) {
        // console.log(1)
        this.$refs.img_3.src = manage_change_image
      } else if (a===1) {
        this.$refs.img_3.src = manage_image
      }
      // console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      this.a = parseInt(key)
      console.log(key)
      if (this.a === 3) {
        console.log(1);
        this.$refs.img_3.src = manage_image
      }
      // console.log(key, keyPath)
    },
  }
}
</script>



